.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.card {
  background: linear-gradient(180deg, #0099AE 0%, #00BF82 100%);
  height: 268px;
  width: 318px;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 5px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  transition: 0.5s;
  overflow: hidden;
  text-align: center;
  font-size: 22px;
  line-height: 140%;
}
.card:hover {
  box-shadow: 0 10px 20px rgb(0 0 0 / 40%);
}

img {
  height: 218px;
  width: 318px;
  border-radius: 5px;
}