.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 110px;
}
.menu-toggle {
  width: 55px;
  height: 55px;
  background-image: url(./../../assets/Rectangle3.svg);
  background-position: center;
  z-index: 10;
  cursor: pointer;
}
.menu-toggle_set-hidden {
  background-image: url(./../../assets/Rectangle2.svg);
}
.switch {
  display: flex;
  align-items: center;
  width: 70px;
  height: 30px;
  border-radius: 30px;
  background-color: #00BF82;
  color: aliceblue;
  padding: 0 5px;
  margin-right: 30px;
}
.switch__handle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgb(250, 246, 247);
  transition: 1s;
}
.switch__text {
  text-transform: uppercase;
  font-size: 12px;
  margin-left:3px;
}
.switch:hover .switch__handle {
  transform: translateX(40px);
}
.switch:hover .switch__text {
  display: none;
}
